import { viewVolunteer, myInfo } from '@/api/index'
import { errorMsg } from '@/utils/common'
export default {
  data () {
    return {
      ask: '',
      volunteerList: [],
      batch: '',
      category: '',
      province: '',
    }
  },
  computed: {
    openid () {
      return this.$store.state.user.openid
    }
  },
  mounted () {
    this.getVolunteer()
    this.getUserInfo()
  },
  methods: {
    // 去页面
    gotoPage (path) {
      this.$router.push({ path: path })
    },
    getUserInfo () {
      myInfo(this.openid).then(res => {
        this.batch = res.data.data.batch || '暂无'
        this.category = res.data.data.category || '暂无'
        this.province = res.data.data.province || '暂无'
      })
      // 获取意向城市
      // getGoalCity({
      //   openid: this.openid
      // }).then(resp => {
      //   if (resp.data.code === 0) {
      //     this.goalCity = resp.data.data.city
      //   }
      // })
    },
    getVolunteer () {
      viewVolunteer({
        openid: this.openid
      }).then(res => {
        if (res.data.code === 0) {
          console.log('志愿单：', res.data.data)
          this.volunteerList = res.data.data
        }
      })
    }
  }
}



